import React, { useCallback, useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  makeStyles,
  Grid,
  TextField, CardHeader, Divider
} from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import useAuth from 'src/hooks/useAuth';
import Hashids from 'hashids'
import {addSlide} from 'src/api/slides'
import Autocomplete from '@material-ui/lab/Autocomplete/Autocomplete';
import Chip from '@material-ui/core/Chip';
import { getListAllTags } from '../../../../api/catalog';
import useIsMountedRef from '../../../../hooks/useIsMountedRef';
import FilesDropzone from '../../../../components/FilesDropzone';
const useStyles = makeStyles(() => ({
  root: {}
}));

const SlideCreateForm = ({ className, ...rest }) => {

  const isMountedRef = useIsMountedRef();
  const classes = useStyles();
  const history = useHistory();
  const { user } = useAuth();
  const hashids = new Hashids();

  const [files, setFiles] = useState([]);
  const handleRemoveAll = () => {
    setFiles([]);
  };
  const handleDrop = useCallback((acceptedFiles) => {
    setFiles((prevFiles) => acceptedFiles);
  }, []);

  const [tag, setTags] = useState([]);
  const getTags = useCallback(async () => {
    try {
      const tag = await getListAllTags();
      console.log(tag);
      if (isMountedRef.current) {
        setTags(tag);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);
  useEffect(() => {
    getTags();
  }, [getTags]);

  const sleep = (ms) => new Promise((r) => setTimeout(r, ms));

  return (
    <Formik
      initialValues={{
        name: '',
        desc:'',
        tag:'',
        type:'master',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        name: Yup.string().max(255).required('Name is required'),
      })}
      onSubmit={async (values, {
        resetForm,
        setErrors,
        setStatus,
        setSubmitting
      }) => {
        try {
          values.created_by=user.userId;
          values.author = user.name;
          console.log(values);
          const idSlide = await addSlide(values,files[0]);
          await sleep(500);
          if(typeof idSlide!=="undefined"){
            history.push('/teacher/management/slides/edit/'+idSlide);
          }
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
          errors,
          handleBlur,
          handleChange,
          handleSubmit,
          isSubmitting,
          touched,
          values
        }) => (
        <form className={clsx(classes.root, className)} onSubmit={handleSubmit}{...rest}>
          <Card>
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label="Tn Slide"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                  <TextField
                    fullWidth
                    name="type"
                    required
                    value="master"
                    hidden
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <Autocomplete
                    fullWidth
                    multiple
                    name="tag"
                    onChange={(event, value) =>values.tag = value}
                    id="tags-filled"
                    options={tag.map((option) => option.name)}
                    freeSolo
                    renderTags={(value, getTagProps) =>
                      value.map((option, index) => (
                        <Chip variant="outlined" label={option} {...getTagProps({ index })} />
                      ))
                    }
                    renderInput={(params) => (
                      <TextField {...params} variant="outlined" label="Tags" placeholder="Thm tag vo cho cho slide"/>
                    )}
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <TextField
                    fullWidth
                    label="M t ngn"
                    name="desc"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.desc}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={12} xs={12}>
                  <Card>
                    <CardHeader title="nh Cover" />
                    <Divider />
                    <CardContent>
                      <FilesDropzone handleDrop = {handleDrop} files={files} handleRemoveAll={handleRemoveAll}/>
                    </CardContent>
                  </Card>
                </Grid>
              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  // disabled={isSubmitting}
                >
                  Lu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

SlideCreateForm.propTypes = {
  className: PropTypes.string
};

export default SlideCreateForm;
